@import url("https://fonts.googleapis.com/css2?family=Poppins:wght@300;400&display=swap");

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: "Poppins", sans-serif;
}

#logo {
  animation: fill 1s ease-in-out forwards 2.6s;
}

@keyframes fill {
  0% {
    fill: transparent;
  }
  100% {
    fill: white;
  }
}

#logo path:nth-child(1) {
  stroke-dasharray: 3657;
  stroke-dashoffset: 3657;
  animation: strokeLine 6s ease-in-out forwards;
}
#logo path:nth-child(2) {
  stroke-dasharray: 1065;
  stroke-dashoffset: 1065;
  animation: strokeLine 4s ease-in-out forwards;
}
#logo path:nth-child(3) {
  stroke-dasharray: 2709;
  stroke-dashoffset: 2709;
  animation: strokeLine 4s ease-in-out forwards;
}
#logo path:nth-child(4) {
  stroke-dasharray: 2293;
  stroke-dashoffset: 2293;
  animation: strokeLine 4s ease-in-out forwards;
}

@keyframes strokeLine {
  100% {
    stroke-dashoffset: 0;
  }
}

.main_div {
  display: flex;
  justify-content: center;
  flex-direction: column;
  color: #fff;
}

.thumbnails,
.videos_main {
  margin: 2rem 0;
}
.thumbnails_h1,
.videos_h1,
.about_h1,
.ids_h1,
.admin_h1 {
  text-align: center;
  border-bottom: 1px solid #000;
  padding-bottom: 0.5rem;
}

body::before {
  content: "";
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100vh;
  background: linear-gradient(45deg, #c31432, #240b36);
  background-size: cover;
  z-index: -10;
  animation: bg 15s ease infinite;
}

@keyframes bg {
  0%,
  100% {
    filter: hue-rotate(0);
  }
  50% {
    filter: hue-rotate(360deg);
  }
}

.navbar-brand img {
  max-width: 15rem;
  filter: invert(1);
}

.header {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.header p {
  font-size: 1.5rem;
}

.header svg:nth-child(2) {
  position: relative;
}

.header svg:nth-child(2)::before {
  content: "";
  position: absolute;
  left: 0;
  bottom: 0;
  width: 0;
  height: 10px;
  background: #111;
  animation: line 1s ease 1.5s forwards;
}

@keyframes line {
  0% {
    width: 0;
  }
  100% {
    width: 100%;
  }
}

/* posts =================================== */

.posts,
.videos {
  margin: 2rem 0;
  display: grid;
  grid-template-columns: repeat(3, minmax(0, 1fr));
  grid-gap: 2rem;
}
.thumb_img {
  position: relative;
}
.thumb_img .content {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  background: rgba(255, 255, 255, 0.5);
  opacity: 0;
  transition: all 0.3s ease;
}
.content a {
  color: #000;
  font-size: 2rem;
  border: 2px solid #000;
  padding: 0.2rem 0.5rem;
  border-radius: 0.3rem;
  transform: translateY(20%);
  opacity: 0;
  transition: all 0.3s ease;
}
.thumb_img:hover .content a {
  opacity: 1;
  transform: translateY(0);
}
.thumb_img:hover .content {
  opacity: 1;
}
.post,
.video {
  box-shadow: 0.2rem 0.2rem 2rem rgba(0, 0, 0, 0.3);
}

.post img,
.video img {
  width: 100%;
}

.post .details,
.video .details {
  padding: 1rem;
}

#about a {
  font-size: 0.96rem;
}

footer {
  background: #f1f1f1;
}
footer p {
  padding: 1rem;
  margin: 0;
}

@media (max-width: 768px) {
  #logo {
    width: 15rem;
    height: 8rem;
  }
  .main_div {
    min-height: calc(100vh - 79.22px);
  }
  /* posts =========================== */
  .posts,
  .videos {
    grid-template-columns: repeat(1, minmax(0, 1fr));
  }
  .navbar-brand img {
    max-width: 11rem;
  }
}
